.category {
    flex-wrap: wrap;
    margin-top: 80px !important;
  }
  .category .card {
      width: 23%;
      min-width: 250px;
      padding: 10px 12px;
      border: 1px solid #cce7d0 !important; 
      border-radius: 25px !important;
      cursor: pointer !important;
      box-shadow: 2px 2px 3px  #cce7d0;
      margin: 8px 8px;
      transition: 0.2s ease;
  }
  .category .card:hover{
    color: #0a9188;
    transform: translateY(-4px);
  }
  .category .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .category .card-price {
    color: green;
    font-weight: bold;
  }
  .category button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  
  .category .card-text {
    color: rgb(90, 89, 89);
  }
  
  .category .card img:hover {
    transform: scale(0.9);
  }
  
  .category .card-img-top {
    height: 300px !important;
    border-radius: 20px;
  }